<template>
<div class="md-pagination">
  <span>{{currentItemCount}} - {{currentPageCount}} {{mdSeparator}} {{this.mdTotal}}</span>

  <md-button class="md-icon-button" @click="goToPrevious()" :disabled="mdPage === 1">
    <md-icon>keyboard_arrow_left</md-icon>
  </md-button>
  <md-button v-for="i in maxPage" :key="i" @click="goToPage(i)" :disabled="i === mdPage">{{i}}</md-button>
  <md-button class="md-icon-button" @click="goToNext()" :disabled="currentPageCount >= mdTotal">
    <md-icon>keyboard_arrow_right</md-icon>
  </md-button>
</div>
</template>

<script>
export default {
  props: {
    mdPageSize: {
      type: [String, Number],
      default: 10
    },
    mdPage: {
      type: Number,
      default: 1
    },
    mdTotal: {
      type: [String, Number],
      default: 'Many'
    },
    mdSeparator: {
      type: String,
      default: 'of'
    }
  },
  data: () => ({
    currentPageSize: 0
  }),
  computed: {
    currentItemCount () {
      return this.mdTotal ? ((this.mdPage - 1) * this.mdPageSize) + 1 : 0
    },
    currentPageCount () {
      let count = this.mdPage * this.mdPageSize
      return count > this.mdTotal ? this.mdTotal : count
    },
    maxPage () {
      let max = Math.floor(this.mdTotal / this.mdPageSize)
      if (this.mdPageSize * max < this.mdTotal) max++
      return max
    }
  },
  watch: {
    mdPageSize: {
      immediate: true,
      handler (pageSize) {
        this.currentPageSize = this.pageSize
      }
    }
  },
  created () {
    this.currentPageSize = this.mdPageSize
  },
  methods: {
    setPageSize () {
      this.$emit('updatePageSize', this.currentPageSize)
    },
    goToPrevious () {
      this.$emit('goToPrevious')
    },
    goToPage (page) {
      this.$emit('goToPage', {page: page})
    },
    goToNext () {
      this.$emit('goToNext')
    }
  }
}
</script>

<style scoped lang="scss">
.md-pagination {
  height: 56px;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  border-top: 1px solid #e8e8e8;
  font-size: 12px;
  .md-button {
    width: 40px;
    min-width: 40px;
    margin: 5px;
  }
}
</style>



