<template>
<div>
  <vue-headful title="ブドウさん - 私の貸付物件"/>
  <div class="subtitle-warper">
    <span class="subTitle">私の貸付物件</span>
    <md-button class="md-icon-button md-raised md-accent button-createnew" to="/my/rentPiece/new">
      <md-icon>add</md-icon>
    </md-button>
  </div>
  <div class="md-layout md-gutter md-alignment-left">
    <div class="md-layout-item md-size-100">
      <md-table>
        <pagination
          :md-total="manager.myRentService.count"
          :md-page="manager.myRentService.page"
          @goToPrevious="goToPrevious"
          @goToPage="goToPage"
          @goToNext="goToNext">
        </pagination>
      </md-table>
    </div>
    <div class="md-layout-item md-size-50 md-xsmall-size-100" v-for="piece in manager.myRentService.rentPieces" :key="piece._id">
      <myRentPieceCard :manager="manager" :object="piece"/>
    </div>
    <div class="md-layout-item md-size-100">
      <md-table>
        <pagination
          :md-total="manager.myRentService.count"
          :md-page="manager.myRentService.page"
          @goToPrevious="goToPrevious"
          @goToPage="goToPage"
          @goToNext="goToNext">
        </pagination>
      </md-table>
    </div>
  </div>
</div>
</template>

<script>
import pagination from '@/components/common/pagination'
import myRentPieceCard from '@/components/shared/myRentPieceCard'
export default {
  props: ['manager'],
  components: {
    myRentPieceCard,
    pagination
  },
  methods: {
    goToPrevious () {
      let toPage = this.manager.myRentService.page - 1
      this.manager.myRentService.page = toPage
      this.manager.myRentService.findRentPiece({}, toPage)
    },
    goToPage (e) {
      this.manager.myRentService.page = e.page
      this.manager.myRentService.findRentPiece({}, e.page)
    },
    goToNext () {
      let toPage = this.manager.myRentService.page + 1
      this.manager.myRentService.page = toPage
      this.manager.myRentService.findRentPiece({}, toPage)
    }
  }
}
</script>

<style scoped lang="scss">
.subtitle-warper{
  position: relative;
  padding: 15px;
}
.subTitle{
  font-size: 30px;
  font-weight: 500;
}
.button-createnew{
  position: absolute;
  top: 0;
  right: 50px;
}
.md-layout-item{
  margin-bottom: 10px;
}
</style>



