<template>
<md-card md-with-hover>
  <md-card-media class="ribbon-box">
    <div class="ribbon-area" v-if="object.isPublishing">
      <span class="ribbon">公開中</span>
    </div>
    <img class="piece-thumbnail" :src="object.photoThumbnails[0]" v-if="object.photoThumbnails && object.photoThumbnails.length > 0">
    <img class="piece-thumbnail" src="@/assets/noimage.png" v-else>
    <div class="piece-actions">
      <md-menu md-size="big" md-direction="bottom-end">
        <md-button class="md-icon-button md-accent md-raised" md-menu-trigger>
          <md-icon>more_vert</md-icon>
        </md-button>
        <md-menu-content>
          <md-menu-item @click="publishPiece(object)" v-if="!object.isPublishing">
            <span>HPへ公開</span>
          </md-menu-item>
          <md-menu-item @click="unPublishPiece(object)" v-else>
            <span>HPから取下</span>
          </md-menu-item>
          <md-menu-item @click="viewPiece(object)">
            <span>詳細</span>
          </md-menu-item>
          <md-menu-item @click="editPiece(object)">
            <span>編集</span>
          </md-menu-item>
          <md-menu-item @click="deletePiece(object)">
            <span>削除</span>
          </md-menu-item>
        </md-menu-content>
      </md-menu>
    </div>
  </md-card-media>

  <md-card-header>
    <div class="md-title">{{object.name}}</div>
    <div class="md-subhead" v-if="object.stations && object.stations.length > 0">
      {{object.stations[0].station}} 徒歩{{object.stations[0].walking}}分
    </div>
  </md-card-header>

  <md-card-content>
    <md-chip class="md-accent" v-if="object.price >= 10000">{{object.priceText}}</md-chip>
    <md-chip class="md-accent" v-else>{{object.price}}円</md-chip>
    <md-chip class="md-primary" v-if="object.layout">{{object.layoutText}}</md-chip>
    <md-chip class="md-primary" v-if="object.isApartment && object.exclusiveArea">{{object.exclusiveAreaText}}</md-chip>
    <md-chip class="md-primary" v-if="object.isDetached && object.buildingArea">{{object.buildingAreaText}}</md-chip>
  </md-card-content>
</md-card>
</template>

<script>
import photoGallery from '@/components/common/photoGallery'
import utils from '@/tool/utils.js'
import manager from '@/store/manager.js'
export default {
  props: ['manager', 'object'],
  components: {
    photoGallery
  },
  methods: {
    publishPiece (piece) {
      manager.mySellService.publishSellPiece(piece, () => {
        manager.mySellService.preloadSellPieces(null, null, () => {
          utils.event.$emit('SCROLL_TO_TOP')
        })
      })
    },
    unPublishPiece (piece) {
      manager.mySellService.unPublishSellPiece(piece, () => {
        manager.mySellService.preloadSellPieces(null, null, null)
      })
    },
    viewPhotos (piece) {
      if (piece.photos && piece.photos.length > 0) utils.event.$emit('SHOW_PHOTOVIEWER', piece.photos)
    },
    viewPiece (piece) {
      if (piece.id) {
        let url = location.protocol + '//' + location.host + '/my/sellPieceDetail/' + piece._id
        utils.openInNewTab(url)
      }
    },
    editPiece (piece) {
      if (piece.id) this.$router.push({path: '/my/sellPiece/' + piece.id})
    },
    deletePiece (piece) {
      let that = this
      if (piece.id) {
        utils.event.$emit('SHOW_MESSAGE', {code: 'I003'}, () => {
          that.manager.mySellService.deleteSellPiece({_id: piece.id})
        })
      }
    }
  }
}
</script>

<style scoped lang="scss">
.md-card {
  margin: 0px;
  height: 100%;
}

.ribbon-box {
    position: relative;
    box-sizing: border-box;
}
.ribbon-area {
  position: absolute;
  top: -6px;
  right: -6px;
  width: 89px;
  height: 91px;
  overflow: hidden;
}
.ribbon {
    display: inline-block;
    position: absolute;
    padding: 7px 0;
    left: -23px;
    top: 22px;
    width: 160px;
    text-align: center;
    font-size: 18px;
    line-height: 16px;
    background: linear-gradient(to right, #6b52b8 0%,#9185be 100%);
    color: #fff;
    letter-spacing: 0.05em;
    -webkit-transform: rotate(45deg);
    -ms-transform: rotate(45deg);
    transform: rotate(45deg);
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.2);
}
.ribbon:before,.ribbon:after{
  position: absolute;
  content: "";
  border-top: 4px solid #563e9e;
  border-left: 4px solid transparent;
  border-right: 4px solid transparent;
  bottom: -4px;
}
.ribbon:before{
  left: 14px;
}
.ribbon:after{
  right: 18px;
}

.piece-thumbnail {
  width: 100%;
  height: 200px;
  object-fit: cover;
}
.piece-actions {
  position: absolute;
  bottom: 0px;
  right: 0px;
  width: 100%;
  padding: 5px;
  display: flex;
  justify-content: flex-end;
  align-items: center;
}
</style>



