<template>
<div>
  <vue-headful title="ブドウさん - 私の売買物件"/>
  <md-button class="md-icon-button md-fab md-raised md-accent add-piece" to="/my/sellPiece/new"><md-icon>add</md-icon></md-button>
  <div class="info-title">
    <md-content class="title-bar md-primary"></md-content><p class="md-title">私の売買物件 ({{manager.mySellService.sellPieces.length}} / {{manager.mySellService.count}})</p>
    <!-- <md-radio class="bd-small-hide option" v-model="hugeSize" :value="false">省略</md-radio>
    <md-radio class="bd-small-hide option" v-model="hugeSize" :value="true">詳細</md-radio> -->
  </div>
  <!-- <div class="bd-small-show">
    <md-radio class="option" v-model="hugeSize" :value="false">省略</md-radio>
    <md-radio class="option" v-model="hugeSize" :value="true">詳細</md-radio>
  </div> -->
  <div class="md-layout md-gutter md-alignment-left" v-if="hugeSize">
    <div class="md-layout-item md-size-50 md-small-size-100" v-for="piece in manager.mySellService.sellPieces" :key="piece._id">
      <mySellPieceDetailCard :manager="manager" :object="piece"/>
    </div>
  </div>
  <div class="md-layout md-gutter md-alignment-left" v-else>
    <div class="md-layout-item md-size-25 md-medium-size-33 md-small-size-50 md-xsmall-size-100" v-for="piece in manager.mySellService.sellPieces" :key="piece._id">
      <mySellPieceCard :manager="manager" :object="piece"/>
    </div>
  </div>
</div>
</template>

<script>
import utils from '@/tool/utils.js'
import manager from '@/store/manager.js'
import mySellPieceCard from '@/components/shared/mySellPieceCard'
import mySellPieceDetailCard from '@/components/shared/mySellPieceDetailCard'
export default {
  props: ['manager'],
  components: {
    mySellPieceCard,
    mySellPieceDetailCard
  },
  data: () => ({
    isProcessing: false,
    hugeSize: false
  }),
  mounted () {
    const that = this
    that.isProcessing = false
    utils.event.$on('SCROLL_ON_BOTTOM', () => {
      if (manager.mySellService.sellPieces.length < manager.mySellService.count && !that.isProcessing) {
        that.isProcessing = true
        that.manager.mySellService.moreSellPieces(() => {
          that.$nextTick(() => {
            that.isProcessing = false
          })
        })
      }
    })
  },
  beforeDestroy () {
    utils.event.$off('SCROLL_ON_BOTTOM')
  }
}
</script>

<style scoped lang="scss">
.add-piece {
  position: absolute;
  top: 85px;
  right: 10px;
}
.info-title {
  display: flex;
  justify-content: flex-start;
  align-items: center;
}
.option {
  margin-left: 20px;
}
.title-bar {
  width: 5px;
  height: 30px;
  border-radius: 1px;
  margin: 0 10px;
}
.md-layout-item {
  margin-bottom: 15px;
}
.more-container {
  width: 100%;
  display: flex;
  justify-content: center;
}
</style>



