import utils from '@/tool/utils.js'
import SellPiece from '@/store/models/sellPiece.js'

class EstateSellPieceService {
  constructor () {
    this._sellPieces = []
    this._count = 0
    this._page = 0
    this._sellPiece = null
    this.searchCondition = {
      stations: [],
      walking: '',
      layouts: '',
      area: '',
      type: '',
      structure: '',
      age: '',
      min: '',
      max: '',
      yearsBefore: ''
    }
  }
  get sellPieces () {
    return this._sellPieces
  }
  set sellPieces (pieces) {
    if (pieces) {
      pieces.forEach(one => {
        // 重複を削除
        this._sellPieces.filter(sellPiece => {
          return sellPiece._id === one._id
        })
        // リストに追加
        this._sellPieces.push(new SellPiece(one))
      })
    }
  }
  get count () {
    return this._count
  }
  set count (data) {
    this._count = parseInt(data)
  }
  get page () {
    return this._page
  }
  set page (data) {
    this._page = parseInt(data)
  }
  get sellPiece () {
    return this._sellPiece
  }
  set sellPiece (piece) {
    if (piece) {
      this._sellPiece = new SellPiece(piece)
    } else {
      this._sellPiece = null
    }
  }

  init () {
    this._sellPieces.splice(0, this._sellPieces.length)
    this.count = 0
    this.page = 0
    this.sellPiece = null
  }

  preloadSellPieces = (to, from, next) => {
    let that = this
    utils.restGet('/public/findSellPieces', {}).then(
      response => {
        if (response) {
          that.init()
          that.count = response.count
          that.sellPieces = response.datas
          if (next) next()
        }
      }
    )
  }

  moreSellPieces (next) {
    let that = this
    utils.restGet('/public/findSellPieces', {filter: {}, page: that.page + 1}).then(
      response => {
        if (response) {
          that.count = response.count
          that.sellPieces = response.datas
          that.page += 1
          if (next) next()
        }
      }
    )
  }

  preloadSellPieceForEdit = (to, from, next) => {
    let that = this
    const reg = /[0-9A-Fa-f]{24}/g
    if (to.params.id && to.params.id !== 'new' && reg.test(to.params.id)) {
      utils.restGet('/public/findSellPieces', {filter: {_id: to.params.id}}).then(
        response => {
          if (response) {
            if (response.datas.length > 0) {
              that.sellPiece = response.datas[0]
              if (next) next()
            } else {
              utils.event.$emit('SHOW_MESSAGE', {code: 'B001', detail: '対象物件を参照する権限がないか、対象物件が削除されたか、いくつかの原因が考えられます。'}, () => {
                utils.router.push({path: '/'})
              })
            }
          }
        }
      )
    } else if (to.params.id && to.params.id === 'new') {
      that.sellPiece = {}
      if (next) next()
    } else {
      utils.event.$emit('SHOW_MESSAGE', {code: 'B009', detail: '物件IDが間違っています。'}, () => {
        utils.router.push({path: '/'})
      })
    }
  }

  preloadSellPieceForDetail = (to, from, next) => {
    let that = this
    const reg = /[0-9A-Fa-f]{24}/g
    if (to.params.id && reg.test(to.params.id)) {
      utils.restGet('/public/findSellPieces', {filter: {_id: to.params.id}}).then(
        response => {
          if (response) {
            if (response.datas.length > 0) {
              that.sellPiece = response.datas[0]
              if (next) next()
            } else {
              utils.event.$emit('SHOW_MESSAGE', {code: 'B001', detail: '対象物件を参照する権限がないか、対象物件が削除されたか、いくつかの原因が考えられます。'}, () => {
                utils.router.push({path: '/'})
              })
            }
          }
        }
      )
    } else {
      utils.event.$emit('SHOW_MESSAGE', {code: 'B009', detail: '物件IDが間違っています。'}, () => {
        utils.router.push({path: '/'})
      })
    }
  }

  preloadSellPieceForPreview = (to, from, next) => {
    let that = this
    const reg = /[0-9A-Fa-f]{24}/g
    if (to.params.id && to.params.id !== 'new' && reg.test(to.params.id)) {
      utils.restGet('/public/findSellPieces', {filter: {_id: to.params.id}}).then(
        response => {
          if (response) {
            if (response.datas.length > 0) {
              that.sellPiece = response.datas[0]
              if (parseInt(to.params.index) >= 0 && parseInt(to.params.index) < that.sellPiece.previews.length) {
                if (next) next()
              } else {
                utils.event.$emit('SHOW_MESSAGE', {code: 'B001'}, () => {
                  utils.router.push({path: '/'})
                })
              }
            } else {
              utils.event.$emit('SHOW_MESSAGE', {code: 'B001', detail: '対象物件を参照する権限がないか、対象物件が削除されたか、いくつかの原因が考えられます。'}, () => {
                utils.router.push({path: '/'})
              })
            }
          }
        }
      )
    } else {
      utils.event.$emit('SHOW_MESSAGE', {code: 'B009', detail: '物件IDが間違っています。'}, () => {
        utils.router.push({path: '/'})
      })
    }
  }
}

export default new EstateSellPieceService()



