<template>
<md-card md-with-hover>
  <md-card-header class="ribbon-box">
    <div class="ribbon-area" v-if="object.isPublishing">
      <span class="ribbon">公開中</span>
    </div>
    <div class="piece-title">
      <md-content class="md-primary piece-type">{{object.typeText}}</md-content>
      <div class="md-title">{{object.name}}</div>
    </div>
  </md-card-header>

  <md-card-content>
    <div class="md-layout">
      <div class="md-layout-item md-size-40 md-medium-size-70">
        <div class="piece-actions">
          <md-button class="md-accent md-raised" @click.stop="publishPiece(object)" v-if="!object.isPublishing">HPへ公開</md-button>
          <md-button class="md-accent md-raised" @click.stop="unPublishPiece(object)" v-if="object.isPublishing">HPから取下</md-button>
        </div>
        <div @click="viewPhotos(object)">
          <photoGallery class="piece-thumbnail" :manager="manager" :photos="object.photoThumbnails"/>
        </div>
        <div class="piece-actions">
          <md-button class="md-accent md-raised md-icon-button" @click.stop="viewPiece(object)"><md-icon>more_horiz</md-icon></md-button>
          <md-button class="md-accent md-raised md-icon-button" @click.stop="editPiece(object)"><md-icon>edit</md-icon></md-button>
          <md-button class="md-accent md-raised md-icon-button" @click.stop="deletePiece(object)"><md-icon>delete</md-icon></md-button>
        </div>
        <div class="detail-row">
          <md-content class="detail-label-ui">アドレス</md-content>
        </div>
        <div class="detail-row" style="margin-bottom: 15px;">
          <div class="detail-info">{{object.address}}</div>
        </div>
        <div class="detail-row">
          <md-content class="detail-label-ui">交通情報</md-content>
        </div>
        <div class="detail-row">
          <p class="detail-info" v-if="object.stations != null && object.stations.length > 0">{{object.stations[0].station}} <span v-if="object.stations[0].walking != null">徒歩{{object.stations[0].walking}}分</span></p>
          <p class="detail-info" v-else> -</p>
        </div>
        <template v-if="object.stations && object.stations.length > 1">
          <div class="detail-row" v-for="(station, index) in object.stations" :key="station._id" v-if="index > 0">
            <p class="detail-info">{{station.station}} <span v-if="station.walking != null">徒歩{{station.walking}}分</span></p>
          </div>
        </template>
      </div>

      <div class="md-layout-item md-size-60 md-medium-size-100">
        <div class="detail-row">
          <div class="detail-label"><md-content class="detail-label-ui">価格</md-content></div>
          <p class="detail-value">{{object.priceText}}</p>
        </div>
        <div class="detail-row" v-if="object.isApartment">
          <div class="detail-label"><md-content class="detail-label-ui">管理費</md-content></div>
          <p class="detail-value">{{object.manageText}}</p>
        </div>
        <div class="detail-row" v-if="object.isApartment">
          <div class="detail-label"><md-content class="detail-label-ui">修繕費</md-content></div>
          <p class="detail-value">{{object.repairText}}</p>
        </div>
        <div class="detail-row" v-if="object.isDetached">
          <div class="detail-label"><md-content class="detail-label-ui">建ぺい率</md-content></div>
          <p class="detail-value">{{object.coverageText}}</p>
        </div>
        <div class="detail-row" v-if="object.isDetached">
          <div class="detail-label"><md-content class="detail-label-ui">容積率</md-content></div>
          <p class="detail-value">{{object.volumeText}}</p>
        </div>
        <div class="detail-row">
          <div class="detail-label"><md-content class="detail-label-ui">間取り</md-content></div>
          <p class="detail-value">{{object.layoutText}}</p>
        </div>
        <div class="detail-row" v-if="object.isApartment">
          <div class="detail-label"><md-content class="detail-label-ui">専有面積</md-content></div>
          <p class="detail-value">{{object.exclusiveAreaText}}</p>
        </div>
        <div class="detail-row" v-if="object.isDetached">
          <div class="detail-label"><md-content class="detail-label-ui">建築面積</md-content></div>
          <p class="detail-value">{{object.buildingAreaText}}</p>
        </div>
        <div class="detail-row" v-if="object.isDetached">
          <div class="detail-label"><md-content class="detail-label-ui">土地面積</md-content></div>
          <p class="detail-value">{{object.landAreaText}}</p>
        </div>
        <div class="detail-row">
          <div class="detail-label"><md-content class="detail-label-ui">階層</md-content></div>
          <p class="detail-value">{{object.floorStoryText}}</p>
        </div>
        <div class="detail-row">
          <div class="detail-label"><md-content class="detail-label-ui">構造</md-content></div>
          <p class="detail-value">{{object.structureText}}</p>
        </div>
        <div class="detail-row">
          <div class="detail-label"><md-content class="detail-label-ui">権利形態</md-content></div>
          <p class="detail-value">{{object.rightsText}}</p>
        </div>
        <div class="detail-row" v-if="object.isApartment">
          <div class="detail-label"><md-content class="detail-label-ui">方位</md-content></div>
          <p class="detail-value">{{object.directionText}}</p>
        </div>
        <div class="detail-row" v-if="object.isApartment">
          <div class="detail-label"><md-content class="detail-label-ui">総戸数</md-content></div>
          <p class="detail-value">{{object.householdsText}}</p>
        </div>
        <div class="detail-row">
          <div class="detail-label"><md-content class="detail-label-ui">築年月/数</md-content></div>
          <p class="detail-value">{{object.builtText}}</p>
        </div>
        <div class="detail-row">
          <div class="detail-label"><md-content class="detail-label-ui">入居可能日</md-content></div>
          <p class="detail-value">{{object.residentableText}}</p>
        </div>
        <div class="detail-row">
          <div class="detail-label"><md-content class="detail-label-ui">現況</md-content></div>
          <p class="detail-value">{{object.availableText}}</p>
        </div>
        <div class="detail-row" v-if="object.isDetached">
          <div class="detail-label"><md-content class="detail-label-ui">接道</md-content></div>
          <p class="detail-value">{{object.connection || '-'}}</p>
        </div>
        <div class="detail-row">
          <div class="detail-label"><md-content class="detail-label-ui">コメント</md-content></div>
          <p class="detail-value">{{object.comment || '-'}}</p>
        </div>
        <div class="detail-row">
          <div class="detail-label"><md-content class="detail-label-ui">取引態様</md-content></div>
          <p class="detail-value">{{object.aspectText}}</p>
        </div>
        <div class="detail-row">
          <div class="detail-label"><md-content class="detail-label-ui">仲介手数料</md-content></div>
          <p class="detail-value">{{object.margin || '-'}}</p>
        </div>
        <div class="detail-row">
          <div class="detail-label"><md-content class="detail-label-ui">掲載日</md-content></div>
          <p class="detail-value">{{object.cdateText}}</p>
        </div>
        <div class="detail-row">
          <div class="detail-label"><md-content class="detail-label-ui">更新日</md-content></div>
          <p class="detail-value">{{object.udateText}}</p>
        </div>
      </div>
    </div>
  </md-card-content>

</md-card>
</template>

<script>
import photoGallery from '@/components/common/photoGallery'
import utils from '@/tool/utils.js'
import manager from '@/store/manager.js'
export default {
  props: ['manager', 'object'],
  components: {
    photoGallery
  },
  methods: {
    publishPiece (piece) {
      manager.mySellService.publishSellPiece(piece, () => {
        manager.mySellService.preloadSellPieces(null, null, () => {
          utils.event.$emit('SCROLL_TO_TOP')
        })
      })
    },
    unPublishPiece (piece) {
      manager.mySellService.unPublishSellPiece(piece, () => {
        manager.mySellService.preloadSellPieces(null, null, null)
      })
    },
    viewPhotos (piece) {
      if (piece.photos && piece.photos.length > 0) utils.event.$emit('SHOW_PHOTOVIEWER', piece.photos)
    },
    viewPiece (piece) {
      if (piece.id) {
        let url = location.protocol + '//' + location.host + '/my/sellPieceDetail/' + piece._id
        utils.openInNewTab(url)
      }
    },
    editPiece (piece) {
      if (piece.id) this.$router.push({path: '/my/sellPiece/' + piece.id})
    },
    deletePiece (piece) {
      let that = this
      if (piece.id) {
        utils.event.$emit('SHOW_MESSAGE', {code: 'I003'}, () => {
          that.manager.mySellService.deleteSellPiece({_id: piece.id})
        })
      }
    }
  }
}
</script>

<style scoped lang="scss">
.md-card {
  margin: 0px;
  height: 100%;
}
.piece-title {
  display: flex;
  justify-content: flex-start;
  align-items: center;
}
.piece-type {
  margin-right: 10px;
  width: 120px;
  padding: 10px;
  border-radius: 20px;
  font-size: 16px;
  font-weight: 500;
  text-align: center;
}

.ribbon-box {
    position: relative;
    box-sizing: border-box;
}
.ribbon-area {
  position: absolute;
  top: -6px;
  right: -6px;
  width: 89px;
  height: 91px;
  overflow: hidden;
}
.ribbon {
    display: inline-block;
    position: absolute;
    padding: 7px 0;
    left: -23px;
    top: 22px;
    width: 160px;
    text-align: center;
    font-size: 18px;
    line-height: 16px;
    background: linear-gradient(to right, #6b52b8 0%,#9185be 100%);
    color: #fff;
    letter-spacing: 0.05em;
    -webkit-transform: rotate(45deg);
    -ms-transform: rotate(45deg);
    transform: rotate(45deg);
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.2);
}
.ribbon:before,.ribbon:after{
  position: absolute;
  content: "";
  border-top: 4px solid #563e9e;
  border-left: 4px solid transparent;
  border-right: 4px solid transparent;
  bottom: -4px;
}
.ribbon:before{
  left: 14px;
}
.ribbon:after{
  right: 18px;
}

.piece-thumbnail {
  cursor: pointer;
}
.piece-thumbnail:hover {
  opacity: 0.9;
}
.piece-actions {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 10px 0px;
  .md-button {
    margin: 6px;
  }
}
.detail-row {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  .md-icon {
    margin: 0 5px 0 0;
  }
}
.detail-label {
  width: 90px;
  margin: 8px;
  display: flex;
  justify-content: flex-end;
}
.detail-label-ui {
  width: fit-content;
  font-size: 12px;
  padding: 0px 10px;
  border-radius: 20px;
  background: #e8e8e8;
}
.detail-value {
  width: calc(100% - 90px);
  border-bottom: 1px solid #e8e8e8;
  margin: 8px 0px;
  padding: 0px 10px;
}
.detail-info {
  width: 100%;
  border-bottom: 1px solid #e8e8e8;
  margin: 8px 0px;
  padding: 0px 10px;
}
</style>



