<template>
<md-app md-waterfall md-mode="fixed">
  <vue-headful title="ブドウさん"/>

  <md-app-toolbar class="md-primary">
    <div class="md-toolbar-row">

      <div class="md-toolbar-section-start">
        <md-tabs class="md-primary" :md-active-tab="route">
          <md-tab id="route-home" md-icon="home" md-label="ホーム" to="/"></md-tab>
          <md-tab id="route-sell" md-icon="location_city" md-label="売買物件" to="/my/sell"></md-tab>
          <md-tab id="route-rent" md-icon="domain" md-label="賃貸物件" to="/my/rent"></md-tab>
          <md-tab id="route-account" md-icon="accessibility" md-label="本人" to="/my/account"></md-tab>
        </md-tabs>
      </div>

      <div class="md-toolbar-section-end">
        <span class="user-name bd-small-hide"><md-icon>person</md-icon>{{manager.user.name}}</span>
        <md-button class="md-icon-button" @click="logout()" v-if="!manager.user.isAnonymous">
          <md-icon>power_settings_new</md-icon>
        </md-button>
        <md-button to="/login" v-else>
          ログイン
        </md-button>
      </div>

    </div>
  </md-app-toolbar>

  <md-app-content>
    <router-view :manager="manager"/>
    <detailDialog :manager="manager"/>
    <photoViewerDialog :manager="manager"/>
  </md-app-content>

</md-app>
</template>

<script>
import utils from '@/tool/utils.js'
import detailDialog from '@/components/shared/detailDialog'
import photoViewerDialog from '@/components/common/photoViewerDialog'
export default {
  props: ['manager'],
  components: {
    detailDialog,
    photoViewerDialog
  },
  data: () => ({
    scrollTop: 0,
    timmer: null
  }),
  computed: {
    route () {
      if (this.$route.name === 'home') {
        return 'route-home'
      } else if (this.$route.name === 'account' || this.$route.name === 'apply') {
        return 'route-account'
      } else if (this.$route.name === 'mySell' || this.$route.name === 'mySellPiece') {
        return 'route-sell'
      } else if (this.$route.name === 'myRent') {
        return 'route-rent'
      }
    }
  },
  mounted () {
    const that = this
    utils.event.$on('SCROLL_TO_TOP', () => {
      that.$nextTick(() => {
        that.scrollTo(0, 300)
      })
    })
    that.scrollTop = 0
    let scrollElement = document.getElementsByClassName('md-app-scroller')[0]
    scrollElement.addEventListener('scroll', this.handleScroll)
  },
  beforeDestroy () {
    utils.event.$off('SCROLL_TO_TOP')
    let scrollElement = document.getElementsByClassName('md-app-scroller')[0]
    scrollElement.removeEventListener('scroll', this.handleScroll)
  },
  methods: {
    handleScroll () {
      let scrollElement = document.getElementsByClassName('md-app-scroller')[0]
      if (this.scrollTop < scrollElement.scrollTop && (scrollElement.scrollHeight - scrollElement.scrollTop - scrollElement.clientHeight) < 600) {
        if (this.timmer) clearTimeout(this.timmer)
        this.timmer = setTimeout(() => {
          utils.event.$emit('SCROLL_ON_BOTTOM')
        }, 100)
      }
      this.scrollTop = scrollElement.scrollTop
    },
    scrollTo (to, duration) {
      const that = this
      if (duration <= 0) return
      if (!document.getElementsByClassName('md-app-scroller') || document.getElementsByClassName('md-app-scroller').length <= 0) return
      let element = document.getElementsByClassName('md-app-scroller')[0]
      let difference = to - element.scrollTop - element.clientHeight
      let perTick = difference / duration * 10

      setTimeout(() => {
        element.scrollTop = element.scrollTop + perTick
        if (element.scrollTop === to) return
        that.scrollTo(to, duration - 10)
      }, 10)
    },
    logout () {
      utils.restGet('/logout').then(
        response => {
          if (response) {
            utils.router.push({name: 'login'})
          }
        }
      )
    }
  }
}
</script>

<style scoped lang="scss">
.md-app {
  width: 100%;
  height: 100%;
  position: relative;
}
.user-name {
  height: 36px;
  font-size: 16px;
  display: flex;
  align-items: center;
  padding: 5px 10px;
}
.md-button {
  margin: 0px;
}
</style>



